/* Button Transition */
.ssmlimd {
  float: left;
  width: 100%;
  display: block;
  padding: 3px 12px;
  border: none;
  background: none;
  color: inherit;
  position: relative;
  z-index: 1;
  border: 1px solid #ddd;
}
.ssmlimd:focus {
  outline: none;
}
/* Aylen */
.ssmlimd.button--aylen {
  background: #fff;
  color: #37474f;
  overflow: hidden;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  border-radius: 5px;
  padding: 11px;
}
.button--aylen.button--inverted {
  background: none;
  color: #fff;
}
.button--aylen::before,
.button--aylen::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  bottom: 100%;
  left: 0;
  z-index: -1;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  -webkit-transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
.button--aylen::before {
  background: #7986cb;
}
.button--aylen::after {
  /* background: #3f51b5; */
  background: #142bb7;
}
.button--aylen:hover {
  color: #fff;
}
.button--aylen:hover::before,
.button--aylen:hover::after {
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
}
.button--aylen:hover::after {
  -webkit-transition-delay: 0.175s;
  transition-delay: 0.175s;
}

