/* Ticket Style */
:root {
  --size: 1;
  --background: #fff;
  --color1: #ccc;
  --color2: #ccc;
  --color3: #bbb;
  --color4: #bbbbbb;
}

.ticket {
  float: left;
  margin: 2%;
  width: 20%;
  height: 150px;
  position: relative;
  transition: all 300ms cubic-bezier(0.03, 0.98, 0.53, 0.99) 0s;
  background: linear-gradient(
    to right,
    var(--color1),
    var(--color2),
    var(--color3),
    var(--color4)
  );
  border-radius: 18px;
  padding: 1px;
  cursor: pointer;
}

.ticket:before,
.ticket:after {
  content: "";
  display: block;
  position: absolute;
  top: 60px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  z-index: 1;
  background-color: var(--background);
}

.ticket:before {
  border: 1px solid var(--color1);
  border-top-color: transparent;
  border-left-color: transparent;
  transform: rotate(-45deg);
  left: -15px;
}

.ticket:after {
  border: 1px solid var(--color4);
  border-top-color: transparent;
  border-left-color: transparent;
  transform: rotate(135deg);
  right: -15px;
}

.ticket-content-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  background: var(--background);
  border-radius: 16px;
  box-shadow: 0 3px 6px rgb(0 0 0 / 16%), 0 3px 6px rgb(0 0 0 / 23%);
  /* box-shadow: 0 0 5px; */
}
