.tooltitle {
position: relative;
display: inline-block;
}

.tooltitle:hover .tooltiptext {
visibility: visible;
opacity: 1;
}

.tooltitle .tooltiptext {
visibility: hidden;
background-color: #5f5d5de0;
color: #fff;
text-align: center;
border-radius: 6px;
padding: 5px 8px;
position: absolute;
z-index: 1;
opacity: 0;
transition: opacity 0.3s;
}

.tooltitle .tooltip-top::after {
content: "";
position: absolute;
top: 100%;
left: 50%;
margin-left: -5px;
border-width: 5px;
border-style: solid;
border-color: #5f5d5de0 transparent transparent transparent;
}

.tooltip-right::after {
content: "";
position: absolute;
top: 50%;
left: 100%;
margin-top: -5px;
border-width: 5px;
border-style: solid;
border-color: transparent transparent transparent #555;
}

.tooltip-top {
bottom: 125%;
left: 50%;
margin-left: -30px;
}
.tooltip-right {
top: -5px;
bottom: auto;
right: 128%;
width: max-content;
}

