@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,300,600);
 a {
	 text-decoration: none;
	 transition: all 0.6s ease;
}
 a:hover {
	 transition: all 0.6s ease;
}
 .menuwid {
	 width: 95% !important;
}
 .app {
	 height: 100vh;
