.mfccb {
  position: relative;
  top: 15px;
  left: 90%;
  cursor: pointer;
}
.mfffccbd {
  border-radius: 50%;
  border: 1px solid #202124;
}

.mfffccbn {
  visibility: hidden;
  border: 1px solid #ddd;
  background: #fff;
  border-radius: 50%;
  height: fit-content;
  cursor: pointer;
  width: fit-content;
  float: right;
  margin: -8px;
}
.mfbcff:hover > .mfffccbn {
  visibility: visible;
}
.mfbc:hover > .mfffccbn {
  visibility: visible;
}


