/* Notes */
.mfnc {
  width: 100%;
  background: #fff;
  color: #363636;
  text-decoration: none;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 0 1rem;
  margin: 2%;
  /* height: 17rem; */
}
.mfnh {
  display: flex;
}
.mfncnls {
  display: flex;
  flex-wrap: wrap;
  padding: 0px;
  margin: 0;
}

.mfnht {
  margin: 10px 0;
  font-size: 18px;
}

.mfnhc {
  display: flex;
  align-items: center;
  /* margin: 10px 0 0 10px; */
}

.mfncs,
.mfnhab {
  font-size: 14px;
  padding: 5px;
  margin: 8px;
}

.mfncs {
  flex-grow: 1;
  max-width: 85%;
  border: 1px solid #ddd;
}

.mfnhab {
  border-radius: 5px;
  cursor: pointer;
  padding: 8px;
  font-weight: bolder;
  background-color: #eee;
  outline: none;
  border: 1px solid #ddd;
}

.mfncnl {
  /* width: 200px;
    height: 200px;
    margin: 10px;
    background-color: lightyellow;
    box-shadow: 2px 2px 3px lightgrey;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    position: relative; */

  /* display: block; */
  position: relative;
  padding: 1em;
  /* width: 46%; */
  min-height: 200px;
  margin: 0.5rem;
  /* background: linear-gradient(top, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.25)); */
  /* background-color: #fffd75; */
  box-shadow: 5px 5px 10px -2px rgb(33 33 33 / 30%);
  z-index: 1;
  border-radius: 5px;
}
.mfnct,
.mfncd {
  /* width: 100%;
  padding: 8px 14px;
  background: transparent;
  text-decoration: none;
  border: 1px solid #ddd; */
  background-color: transparent;
  border: none;
  resize: vertical;
  width: 100%;
  padding: 5px;
}
.mfncd:focus,
.mfnct:focus,
.mfnhab:focus {
  outline: none !important;
}

.mfnct {
  /* font-size: 16px;
  font-weight: bold; */

  font-size: 20px;
  line-height: 1.2;
  color: #000000;
  height: 35px;
  margin-top: 10px;
}
.mfncnl textarea:focus {
  outline: none;
  border: none;
  box-shadow: 0 0 5px 1px rgb(0 0 0 / 20%) inset;
}

.mfncd {
  flex-grow: 1;
  font-size: 14px;
  min-height: 150px;
}

.mfncnd {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #E01C12;
  text-align: center;
  line-height: 16px;
  padding: 10px;
  border-color: #B30000;
  font-style: 1.6em;
  font-weight: bolder;
 
  color: #FFFFFF;
  box-shadow: 1px 1px 3px rgb(0 0 0 / 30%), inset 0 -1px 2px -1px rgb(0 0 0 / 50%), inset 0 1px 2px 1px rgb(255 255 255 / 30%);
  text-shadow: 0 -1px 0 rgb(0 0 0 / 30%), 0 1px 0 rgb(255 255 255 / 30%);
  text-decoration: none;
  transition: transform 0.15s, background 0.01s;
  
  backface-visibility: hidden;
}

.mfncnd:hover {
  color: #fff;
  cursor: pointer;
}
.mfnvab {
  text-decoration: underline;
  color: #337ab7;
  cursor: pointer;
  position: absolute;
  right: 2%;
}
.mfncdt {
  font-size: 10px;
}
.mfncsbn {
  height: 103%;
  width: 0;
  position: fixed;
  z-index: 13;
  top: 0;
  right: 0;
  background-color: #F1F3F4;
  /* background: #fff; */
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 0px;
}
.mfncsb {
  width: 98%;
  padding: 1%;
  /* position: absolute;
    right: 10px;
    top: 15%; */
}
.mfncsbnc {
  position: absolute;
  top: 0px;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  color: #111;
  cursor: pointer;
}

