.simpleDivButton{
    height: 30px;
    flex: 1 1 auto;
    padding: 5px 20px;
    position: relative;
    -webkit-box-flex: 1;
    margin:0 15px;
    cursor: pointer;
    border: 1px solid #e3e4e9;
    font-weight: bold;
    color:#555;
    border-radius: 4px;
}
.simpleDivButton p{
    margin-top: 0px !important;
    font-size: 0.75rem;
}
.simpleDivButton span{
    height: 14px;
    min-width: 20px;
    font-size: 0.563rem;
    font-weight: bold;
    color: #fff;
    background-color: #f50057;

    display: flex;
    padding: 0 6px;
    z-index: 1;
    position: absolute;
    top: 0;
    right: 0;
    transform: scale(1) translate(50%, -50%);
    transform-origin: 100% 0%;
    
    flex-wrap: wrap;
    font-size: 0.75rem;
    min-width: 20px;
    box-sizing: border-box;
    transition: transform 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    align-items: center;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 1;
    align-content: center;
    border-radius: 10px;
    flex-direction: row;
    justify-content: center;
}

