
#loginbg{ animation: chBg 20s infinite; 
  animation-duration: 20s;
  position: relative;
  animation-iteration-count: infinite; 
  animation-timing-function: ease-in;    
}
@keyframes chBg{
   0%  {background-image: url("https://247848-766948-raikfcquaxqncofqfm.stackpathdns.com/wp-content/uploads/2018/05/empire_state.jpg");
       
        }
   25% {background-image: url("https://image.freepik.com/free-photo/observation-urban-building-business-steel_1127-2397.jpg");
            
   }
   50% {background-image: url("https://image.freepik.com/free-photo/shanghai-night-china_1127-4487.jpg");
      
   }
   75% {background-image: url("https://image.freepik.com/free-photo/cityscape-shanghai-sunrise-panoramic-view-pudong-business-district-skyline-from-bund_136401-2087.jpg");}
   100%  {background-image: url("https://247848-766948-raikfcquaxqncofqfm.stackpathdns.com/wp-content/uploads/2018/05/empire_state.jpg");}
}

@keyframes mymove {
   from {
       background-position: 0% 0%;
   }
   to {
       background-position: 0% -100%;
   }
}

#ulanimate{
