 .cb-slideshow,
.cb-slideshow:after { 
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 0; 
}
.cb-slideshow:after { 
    content: '';
    /* background: transparent url("https://image.freepik.com/free-photo/shanghai-night-china_1127-4487.jpg") repeat top left;  */
}

.cb-slideshow li span { 
   width: 100%;
   height: 100%;
   position: absolute;
   top: 0px;
   left: 0px;
   color: transparent;
   background-size: cover;
   background-position: 50% 50%;
   background-repeat: none;
   opacity: 0;
   z-index: 0;
   animation: imageAnimation 36s linear infinite 0s; 
   border-radius: 8px;
}

.cb-slideshow li:nth-child(1) span { 
   /* background-image: url("https://247848-766948-raikfcquaxqncofqfm.stackpathdns.com/wp-content/uploads/2018/05/empire_state.jpg")  */
}
.cb-slideshow li:nth-child(2) span { 
   background-image: url("https://image.freepik.com/free-photo/observation-urban-building-business-steel_1127-2397.jpg");
   animation-delay: 10s; 
}
.cb-slideshow li:nth-child(3) span { 
   background-image: url("https://image.freepik.com/free-photo/shanghai-night-china_1127-4487.jpg");
   animation-delay: 20s; 
}

.cb-slideshow li:nth-child(4) span { 
   background-image: url("https://image.freepik.com/free-photo/cityscape-shanghai-sunrise-panoramic-view-pudong-business-district-skyline-from-bund_136401-2087.jpg");
   animation-delay: 30s; 
}
.cb-slideshow li:nth-child(5) span { 
   background-image: url("https://247848-766948-raikfcquaxqncofqfm.stackpathdns.com/wp-content/uploads/2018/05/empire_state.jpg");
   animation-delay: 40s; 
}
.cb-slideshow li:nth-child(6) span { 
   background-image: url("https://image.freepik.com/free-photo/observation-urban-building-business-steel_1127-2397.jpg");
   animation-delay: 50s; 
}

@keyframes imageAnimation { 
   0% { opacity: 1; animation-timing-function: ease-in; }
   18% { opacity: 0; animation-timing-function: ease-out; }
   35% { opacity: 1 ; animation-timing-function: ease-out;}
   50% { opacity: 0 ; animation-timing-function: ease-in;}
   62% { opacity: 1 ;animation-timing-function: ease-out;}
   80% { opacity: 0 ;animation-timing-function: ease-in;}
   100% { opacity: 1 ;animation-timing-function: ease-out;}
}