/* Page 404 Css */
.c{
  text-align: center;
  display: block;
  position: relative;
  width:80%;
  margin: auto;
}
._404{
  font-size: 5rem;
  position: relative;
  display: inline-block;
  z-index: 2;
  height: 240px;
  padding-top: 120px;
  letter-spacing: 15px;
}
._1{
  text-align: center;
    display: block;
    position: relative;
    letter-spacing: 12px;
    font-size: 1.5em;
    line-height: 80%;
    /* padding-top: 20%; */
}
._1::first-letter {
  font-size: 2.5rem;
}
._2{
  text-align:center;
  display:block;
  position: relative;
  font-size: 1.25rem;
  padding: 13px 0px;
  color: #7a827f;
}
.text{
  font-size: 70px;
  text-align: center;
  position: relative;
  display: inline-block;
  margin: 19px 0px 0px 0px;
  /* top: 256.301px; */
  z-index: 3;
  width: 100%;
  line-height: 1.2em;
  display: inline-block;
}


.btn{
  background-color: rgb( 255, 255, 255 );
  position: relative;
  display: inline-block;
  width: fit-content;
  padding: 5px;
  z-index: 5;
  font-size: 25px;
  margin:0 auto;
  color:#33cc99;
  text-decoration: none;
  margin-right: 10px;
  border-radius: 5px;
}
.right{
  float:right;
  width:60%;
}

[data-title]:hover:after {
  opacity: 1;
  transition: all 0.1s ease 0.5s;
  visibility: visible;
}
[data-title]:after {
  content: attr(data-title);
  background-color: #5f5d5de0;
  color: #fff;
  font-size: 0.7rem;
  font-weight: 500;
  position: absolute;
  padding: 3px 8px;
  bottom: -2.5em;
  /* left: 4.5rem;
  white-space: nowrap;
  /* box-shadow: 1px 1px 3px #222222; */
  opacity: 0;
  /* border: 1px solid #111111; */
  z-index: 99999;
  line-height: 1.5;
  visibility: hidden;
  border-radius: 3px;
}
[data-title] {
  position: relative;
}
 
