.sidenav {
    height: 103%;
    width: 0;
    position: fixed;
    z-index: 13;
    top: 0;
    right: 0;
    background-color: rgba(100, 100, 100, 0.3);
    /* background: #fff; */
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 0px;
}

.sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.sidenav a:hover {
    color: #f1f1f1;
}

.sidenav .closebtn {
    position: absolute;
    top: 5%;
    right: 3rem;
    font-size: 36px;
    margin-left: 50px;
    color:#111;
    cursor:pointer;
}
.SideBarComponent{
    width: 99%;
    position: absolute;
    right: 1rem;
    top: 10%;
}