@-webkit-keyframes imageAnimation { 
   0% { opacity: 1; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
   18% { opacity: 0; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
   35% { opacity: 1 ; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
   50% { opacity: 0 ; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in;}
   62% { opacity: 1 ;-webkit-animation-timing-function: ease-out;animation-timing-function: ease-out;}
   80% { opacity: 0 ;-webkit-animation-timing-function: ease-in;animation-timing-function: ease-in;}
   100% { opacity: 1 ;-webkit-animation-timing-function: ease-out;animation-timing-function: ease-out;}
}

@keyframes imageAnimation { 
   0% { opacity: 1; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
   18% { opacity: 0; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
   35% { opacity: 1 ; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
   50% { opacity: 0 ; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in;}
   62% { opacity: 1 ;-webkit-animation-timing-function: ease-out;animation-timing-function: ease-out;}
   80% { opacity: 0 ;-webkit-animation-timing-function: ease-in;animation-timing-function: ease-in;}
   100% { opacity: 1 ;-webkit-animation-timing-function: ease-out;animation-timing-function: ease-out;}
}
