/* Calendar */

.wcalendar {
  display: block;
  position: relative;
  width: 100%;
  background: #fff;
  background: var(--neutral-color);
  border: 1px solid #dadce0;
  border: 1px solid var(--border-color);
}

.wcalendar .header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 1.5em 0;
  border-bottom: 1px solid #dadce0;
  border-bottom: 1px solid var(--border-color);
}

.wcalendar .header .icon {
  cursor: pointer;
  transition: 0.15s ease-out;
}

.wcalendar .header .icon:hover {
  -webkit-transform: scale(1.75);
          transform: scale(1.75);
  transition: 0.25s ease-out;
  color: #1a8fff;
  color: var(--main-color);
}

.wcalendar .header .icon:first-of-type {
  margin-left: 1em;
}

.wcalendar .header .icon:last-of-type {
  margin-right: 1em;
}

.wcalendar .wdays {
  text-transform: uppercase;
  font-weight: 400;
  color: #ccc;
  color: var(--text-color-light);
  font-size: 70%;
  /* padding: 0.75em 0;
  border-bottom: 1px solid var(--border-color); */
}

.wcalendar .wbody .wcell {
  position: relative;
  height: 2.5em;
  border-right: 1px solid #dadce0;
  border-right: 1px solid var(--border-color);
  overflow: hidden;
  cursor: pointer;
  background: #fff;
  background: var(--neutral-color);
  transition: 0.25s ease-out;
}


.wcalendar .wbody .weecell {
  position: relative;
  height: 2.5em;
  /* border-right: 1px solid var(--border-color); */
  overflow: hidden;
  cursor: pointer;
  background: #fff;
  background: var(--neutral-color);
  transition: 0.25s ease-out;
}


.wcalendar .wbody .wcell:hover {
  background: #F6F6F6;
  background: var(--bg-color);
  transition: 0.5s ease-out;
}

.wcalendar .wbody .selected {
  border-left: 10px solid transparent;
  border-image: linear-gradient(45deg, #1a8fff 0%, #53cbf1 40%);
  border-image-slice: 1;
}

.wcalendar .wbody .row {
  border-bottom: 1px solid #dadce0;
  border-bottom: 1px solid var(--border-color);
}

.wcalendar .wbody .row:last-child {
  border-bottom: none;
}

.wcalendar .wbody .wcell:last-child {
  border-right: none;
}

.wcalendar .wbody .wcell .number {
  position: absolute;
  font-size: 82.5%;
  line-height: 1;
  top: 0.75em;
  /* right: 0.75em; */
  font-weight: 700;
}

.wcalendar .wbody .disabled {
  color: #ccc;
  color: var(--text-color-light);
  pointer-events: none;
}

.wcalendar .wbody .wcell .bg {
  font-weight: 700;
  line-height: 1;
  color: #1a8fff;
  color: var(--main-color);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -0.2em;
  right: -0.05em;
  transition: 0.25s ease-out;
  letter-spacing: -0.07em;
}

.wcalendar .wbody .wcell:hover .bg,
.wcalendar .wbody .selected .bg {
  opacity: 0.05;
  transition: 0.5s ease-in;
}

.wcalendar .wbody .col {
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-basis: calc(100% / 7);
          flex-basis: calc(100% / 7);
  width: calc(100% / 7);
}

.dcalendar .dbody .dcol {
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-basis: calc(100%);
          flex-basis: calc(100%);
  width: calc(100%);
}
