div.tooltip {	
    display:inline-block;
    position:relative;
    text-align:left;
    margin:12px 0; 
    min-width:200px;
    max-width:400px;
    top:-20px;
    left:50%;
    transform:translate(-30%,-100%);
    padding:10px 20px;
    color:black;
    background-color:#fff;
    font-weight:normal;
    font-size:14px;
    border-radius:8px;
    position:absolute;
    z-index:99999999;
    box-sizing:border-box;
    box-shadow:0 1px 8px rgba(0,0,0,0.5);  
    display:block;
} 
