ul.breadcrumb {
    padding: 10px 16px;
    list-style: none;
    /* background-color: #eee; */
  }
  
  /* Display list items side by side */
  ul.breadcrumb li {
    display: inline;
    font-size: 0.75rem;
  }
  
  /* Add a slash symbol (/) before/behind each list item */
  ul.breadcrumb li+li:before {
    padding: 8px;
    color: black;
    content: "/\00a0";
  }
  
/* Add a color to all links inside the list */
ul.breadcrumb li  {
    color: #0275d8;
    text-decoration: none;
}
ul.breadcrumb span{
    font-size: 0.75rem;
    font-weight: bold;
}  
